  right: -270px !important;
  -webkit-transition: right 0.1s !important;
  transition: right 0.1s !important; }

.navigation-container.open .menu {
  right: 0 !important; }

.navigation-container.open .line {
  background: #000000 !important; }

.navigation-container.close .menu {
