  .mini-pages-layout div[class^="StyledBox"] {
    -webkit-flex-basis: 100% !important;
            flex-basis: 100% !important;
    -webkit-flex-wrap: wrap !important;
            flex-wrap: wrap !important; }
    @media (min-width: 600px) {
